.planet_hero {
	display: flex;
	margin: auto;
	justify-content: center;

	position: relative;
}

.planet_svg {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.planet_hero_geology_overlay {
	position: absolute;
	top: 70%;

	img {
		max-width: 163px;
		max-height: 199px;
	}
}